.tooltip {
  opacity: 0;
  transition: opacity 0;
  position: absolute;
  left: 50%;
  margin: 0;
  min-width: 100%;
  bottom: 69px;
  text-align: center;
  border-radius: 2px;
  padding: 5px 10px;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 1);
  pointer-events: none;

  p {
    color: black;
    font-size: 12px;

    &:after {
      content: "";
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid #fff;
      position: absolute;
      left: 50%;
      bottom: -7px;
      transform: translateX(-50%);
    }
  }
}

.tooltip_hover {
  position: relative;

  &:hover {
    .tooltip {
      opacity: 1;
    }
  }
}
