@import "./window";
@import "./timeline";
@import "./statistics";
@import "./home";
@import "./taskbar";
@import "./social";
@import "./browser";
@import "./tooltip";

* {
  margin: 0;
  padding: 0;
  font-family: arial;
  transition: opacity 0.5s;
}

ul {
  list-style: none;
}

body,
#root {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(10, 0, 94, 1) 100%
  );
}

#root {
  background: url(https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/assets/wallpaper.png);
  background-size: cover;
  background-position: center;
}
