#home {
  #avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 40px;
  }

  content > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    > div {
      display: flex;
      margin-bottom: 20px;

      > div {
        justify-content: center;
        display: flex;
        flex-direction: column;
      }

      h3 {
        display: block;
        font-size: 26px;
        margin: 2px 0;

        &:last-child {
          font-size: 24px;
        }
      }
    }

    > * {
      width: 600px;
    }

    p {
      font-size: 18px;
      margin: 10px 0;
    }

    p,
    h3,
    img {
      filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.25));
    }
  }
}
